.scrolling {

  overflow-x: scroll;
  overflow-y: hidden;
  white-space: nowrap;
  box-sizing: border-box;
  width: 100%;
  position: relative;
  -webkit-overflow-scrolling: touch;

  
  .card {
    display: inline-block;
    width: 100%;
    height: 100%;
    position: relative;
    overflow: hidden;
    
  }
}
.scrolling::-webkit-scrollbar {
  display: none;
}

.cardbox {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;

}
.cardcontainer {
  width: 65vw;
  height: 40.5vw;
  background-color: black;
  border: 3px solid aliceblue;
  border-radius: 10px;
  position: relative;
  display: flex;
  overflow: hidden;
  margin-top:5vh;
  min-height:30rem;
  
}

@media screen and (max-width:1024px){
  .cardcontainer{
    height:53vw;
    width:75vw;
    margin-top:-1vw;
  }
}

@media screen and (max-width:768px){
  .cardcontainer{
    height:90%;
    width:90%;

    /*
    height:40rem;
    width: 68vw;
    min-width:28.125rem;
    */
  }
}


@media screen and (max-width:499px){
  .cardcontainer{
    /*
    min-width:16.875rem;
    */
  }
}


.cardcontainer:hover {
  box-shadow: 0 0 7px 3px rgb(255, 255, 255, 0.5),
    /* inner white */ 0 0 12px 7px rgb(245, 245, 245, 0.4),
    /* middle magenta */ 0 0 17px 11px rgb(235, 235, 235, 0.3); /* outer cyan */
}

.img {
  width: 80%;
  height: 85%;
  margin-top: 3.5rem;
  margin-left: 25%;
  position: absolute;
  margin-bottom: auto;
  perspective: 1000px;
}
@media screen and (max-width:768px) {
  .img{
  
    width:100%;
    margin-left:1%;
