.textcontainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  z-index: 9;
  width: 53%;
  height: 100%;

  left: 0;
  position: relative;

}

@media screen and (max-width:768px){
  .textcontainer{
   height:40%;
   width: 100%;
   bottom:0;
   display: flex;
   justify-content: center;
   align-items: center;
   position: absolute;
  }
}

.header {
  font-size: 1.6rem;
