.underlay {
  height: 90%;
  margin-top: 2.5%;

  width: 80%;
  animation: underlay-slide-right 1s;
}

.arrowdivforward {
  top: 50%;
  z-index: 99;
  height: content-fit;
  position: absolute;
  color: aliceblue;
  font-size: 5vw;
  width: auto;

}

@media screen and (max-width: 1024px){
  .arrowdivforward{
    font-size:6vw
  }
}

@media screen and (max-width: 768px){
  .arrowdivforward{
   visibility: hidden;
   height: 0;
   width: 0;
  }
}
.arrowdivforward:hover{
  cursor: pointer;
} 

.arrowdivback {
  top: 50%;
  z-index: 99;
  height: content-fit;
  position: absolute;
  color: aliceblue;
  font-size: 5vw;
  width: auto;
 
