}

.mobileforward {
  top: 50%;
  z-index: 99;
  height: content-fit;
  position: absolute;
  color: aliceblue;
  font-size: 5vw;
  width: auto;
  visibility: hidden;

}

@media screen and (max-width: 768px){
  .mobileforward{
   visibility: visible;

