}


.mobileback {
  top: 50%;
  z-index: 99;
  height: content-fit;
  position: absolute;
  color: aliceblue;
  font-size: 5vw;
  width: auto;
  visibility: hidden;
}

@media screen and (max-width: 768px){
  .mobileback{
   visibility: visible;

  }
}


.projectslink {
  width: auto;
  height: auto;
  bottom: 0;
  position: absolute;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
 
  margin-left:50%;
  
}
@media screen and (max-width: 768px) {
  .projectslink {
    visibility: hidden;
    height: 0;
    width: 0;
  }
}
.projectslink:hover {
  scale: 1.1;
  cursor: pointer;
}

.arrowdown {

  color: aliceblue;
  font-size: 2rem;
}

.projectslink:hover .arrowdown {
  animation: arrow-move 0.9s infinite;
}

.hidden {
  visibility: hidden;
  position: absolute;
}

@keyframes slide-left {
  from {
    margin-left: 150%;
  }
  to {
    margin-left: 0%;
  }
}

@keyframes slide-right {
  0% {
    margin-left: -50%;
  }

  100% {
    margin-left: 5%;
  }
}
@keyframes underlay-slide-right {
  0% {
    margin-left: -50%;
  }

  100% {
    margin-left: 0%;
  }
}

@keyframes top-down {
  0% {
    margin-top: -50%;
    opacity: 0%;
  }
  25% {
    opacity: 25%;
  }
  50% {
    opacity: 50%;
  }
  75% {
    opacity: 75%;
  }

  100% {
    margin-top: 5%;
    opacity: 100%;
  }
}

@keyframes arrow-move {
  0% {
    transform: translate(0px, 2.5px);
  }
  25% {
    transform: translate(0px, 5);
  }
  100% {
    transform: translate(-0px, -3px);
  }
}


