.container {

  width: 100%;
  display: flex;
  flex-direction: row;
  position: relative;
  justify-content: center;
  align-items: center;
  overflow: hidden;

}

.h1 {
  color: aliceblue;
  font-size: 3.2vw;
  font-family: sans-serif;
  position: absolute;
  top:5vw;
  height: 15%;

  animation: top-down 0.75s;
}

@media screen and (max-width:1200px) {
  .h1{
    font-size:2.5rem;
  }
}

@media screen and (max-width:1200px) {
  .h1{
    top:4rem;
  }
}


.formcontainer {
  display: flex;
  flex-direction: column ;
  justify-content: center;
  align-items: center;
  width: 80%;
  height: 81%;
  animation: 1fade-in 0.75s;
  font-family: sans-serif;
  margin-top:2.6%;

}
@media screen and (max-width:1200px) {
  .formcontainer{
    
  }
}

.form {
  height: 100%;
  width: 100%;

}
input[type="text"] {
  font-size: 0.9rem;
  text-indent: 1rem;
}

input[type="email"] {
  font-size: 0.9rem;
  text-indent: 1rem;
}



.firstlastcontainer {
  display: flex;
  flex-direction: row;
  width: 100%;
  height: 13%;
  justify-content: space-between;
}

.first {
  width: 46%;
  height: 100%;
  background-color: rgb(216, 221, 231);
  border: 2px solid aliceblue;
  border-radius: 5px;
  color: black;
}
.first:focus{
  box-shadow:
  0 0 4px 2px rgb(255, 255, 255, .5),  /* inner white */
  0 0 6px 3px rgb(245, 245, 245, .4), /* middle magenta */
  0 0 9px 5px rgb(235, 235, 235, .3 ); /* outer cyan */
}
.last {
  width: 46%;
  height: 100%;
  background-color: rgb(216, 221, 231);
  border: 2px solid aliceblue;
  color: black;
  right: 0;
  border-radius: 5px;
}

.last:focus{
  box-shadow:
  0 0 4px 2px rgb(255, 255, 255, .5),  /* inner white */
  0 0 6px 3px rgb(245, 245, 245, .4), /* middle magenta */
  0 0 9px 5px rgb(235, 235, 235, .3 ); /* outer cyan */
}

.email {
  width: 100%;
  height: 13%;
  margin-top: 4%;
  background-color: rgb(216, 221, 231);
  border: 2px solid aliceblue;
  color: black;
  border-radius: 5px;
}

.email:focus{
  box-shadow:
  0 0 4px 2px rgb(255, 255, 255, .5),  /* inner white */
  0 0 6px 3px rgb(245, 245, 245, .4), /* middle magenta */
  0 0 9px 5px rgb(235, 235, 235, .3 ); /* outer cyan */
}

.message {
  width: 100%;
  margin-top: 4%;
  background-color: rgb(216, 221, 231);
  color:black;
  border: 2px solid aliceblue;
  border-radius: 5px;
  font-size:0.9rem;
  font-family: sans-serif;
  text-indent: 1rem;
  padding-top: 10px;
}
.message:focus{
  box-shadow:
  0 0 4px 2px rgb(255, 255, 255, .5),  /* inner white */
  0 0 6px 3px rgb(245, 245, 245, .4), /* middle magenta */
  0 0 9px 5px rgb(235, 235, 235, .3 ); /* outer cyan */
}
.button {
  width: 20%;
  height: 10%;
  margin-top: 2.6%;
  background-color: rgb(25, 25, 25);
  color: aliceblue;
  border: 2px aliceblue solid;
  font-family: sans-serif;
  font-size:0.9rem;
  border-radius: 5px;
}

.button:hover{
  cursor: pointer;
  background-color: aliceblue;
  color: black;
  font-weight: bold;
}

.underlay1 {
  height: 33.5vw;
  width: 39.5vw;
  position: absolute;
  margin-top: 10%;
  background-color: black;
  border: 2px aliceblue solid;
  border-radius: 10px;
  display: flex;
  justify-content: center;
  align-items:center;
  min-height:28rem;
  
}

@media screen and (max-width:1024px) {
  .underlay1{
    width:50vw;
    height:45vw;
  }
}

@media screen and (max-width:768px) {
  .underlay1{
    /*
    width: 68vw;
    min-width:28.125rem;
    height:29rem;
    */
    width:90%;
    height:70%;
  }
}

.underlay1:hover{
  box-shadow:
  0 0 7px 3px rgb(255, 255, 255, .5),  /* inner white */
  0 0 12px 7px rgb(245, 245, 245, .4), /* middle magenta */
  0 0 17px 11px rgb(235, 235, 235, .3 ); /* outer cyan */

}

.underlay2 {
  margin-top: 7.5%;
  margin-left: 0%;
  height: 80%;
  width: 50%;
  background-color: black;
  color: aliceblue;
  top: 0;
  position: absolute;
  z-index: 99;
}

.hidden {
  visibility: hidden;
  position: absolute;
}

@keyframes top-down {
  0% {
   top: -1vw;
    opacity: 0%;
  }
  25% {
    opacity: 25%;
  }
  50% {
    opacity: 50%;
  }
  75% {
    opacity: 75%;
  }

  100% {
    top: 5vw;
    opacity: 100%;
  }
}

@keyframes fade-in {
  0% {
    opacity: 0%;
  }
  25% {
    opacity: 25%;
  }
  50% {
    opacity: 50%;
  }
  75% {
    opacity: 75%;
  }

  100% {
    opacity: 100%;
  }
}
