.container {
  height: 100vh;
  max-height: 100vh;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
}

@media screen and (max-width: 768px) {
  .container {
    flex-direction: column-reverse;
  }
}

.flexitem1 {
  width: 54vw;
  height: 38.5vw;
  display: flex;
  justify-content: center;
  position: absolute;
  flex-direction: column;
  padding-left: 6.5vh;
  left: 0;
  align-items: center;
  border: 2px solid pink;
}

@media screen and (max-width: 1024px) {
  .flexitem1 {
    height: 53vw;
    padding-left: none;
  }
}

@media screen and (max-width: 768px) {
  .flexitem1 {
    position: relative;
    height: 60%;
    left: auto;
    width: 90%;
  }
}

@media screen and (min-width: 300px) and (max-width: 500px) {
  .flexitem1 {
