.projectslink {
  width: auto;
  height: auto;
  bottom: 0;
  position: absolute;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

@media screen and (max-width: 768px) {
  .projectslink {
    visibility: hidden;
    height: 0;
    width: 0;
  }
}

.linkheader {
  color: aliceblue;
  font-size: 16px;
  font-family: sans-serif;
}

.projectslink:hover {
  scale: 1.1;
  cursor: pointer;
}

.arrowdown {
  margin-top: 5%;
  margin-bottom: 5%;
  color: aliceblue;
  font-size: 2rem;
}



.projectslink:hover .arrowdown {
  animation: arrow-move 0.9s infinite;
}

.br {
  visibility: hidden;
}
@media screen and (max-width: 768px) {
  .br {
    visibility: visible;
  }
}
.hidden {
  visibility: hidden;
}

@keyframes slide-right {
  0% {
    margin-left: -50%;
    opacity: 0%;
  }
  25% {
    opacity: 25%;
  }
  50% {
    opacity: 50%;
  }
  75% {
    opacity: 75%;
  }

  100% {
    margin-left: 0%;
    opacity: 100%;
  }
}

@keyframes fade-in-img {
  33% {
    opacity: 33%;
  }
  66% {
    opacity: 66%;
  }
  100% {
    opacity: 100%;
  }

  100% {
    opacity: 100%;
  }
}

@keyframes fade-in {
  0% {
    opacity: 0%;
  }
  25% {
    opacity: 25%;
  }
  50% {
    opacity: 50%;
  }
  75% {
    opacity: 75%;
  }

  100% {
    opacity: 100%;
  }
}

@keyframes arrow-move {
  0% {
    transform: translate(0px, 2.5px);
  }
  25% {
    transform: translate(0px, 5);
  }
  100% {
    transform: translate(-0px, -3px);
  }
}

@keyframes slideDown {
  0% {
    opacity: 0;
    transform: translateY(-700%);
  }
  100% {
    opacity: 1;
    transform: translateY(-0%);
  }
}

@keyframes slideDown2 {
  0% {
    opacity: 0;
    transform: translateY(-500%);
  }
  100% {
    opacity: 1;
    transform: translateY(-0%);
  }
}

@keyframes slideDown3 {
  0% {
    opacity: 0;
    transform: translateY(-450%);
  }
  100% {
    opacity: 1;
    transform: translateY(-0%);
  }
}

@keyframes slideDown4 {
  0% {
    opacity: 0;
    transform: translateY(-875%);
  }
  100% {
    opacity: 1;
    transform: translateY(-0%);
  }
}

@keyframes slideDown5 {
  0% {
    opacity: 0;
    transform: translateY(-850%);
  }
  100% {
    opacity: 1;
    transform: translateY(-0%);
  }
}

.slideDownAnimation {
  animation: slideDown 2s linear ease-in-out;
  animation-name: slideDown;
  animation-duration: 0.5s;
  animation-timing-function: linear;
  animation-fill-mode: forwards;
  animation-delay: 0.25s;
}

.slideDownAnimation2 {
  animation: slideDown2 2s linear ease-in-out;
  animation-name: slideDown2;
  animation-duration: 0.5s;
