.footer {
  height: 25vh;
  width: 100%;
  background-color: rgb(25, 25, 25);
  display: flex;
  font-family: sans-serif;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.titlebox {
  width: 100%;
  height: 30%;
  top: 0;
  display: flex;
  align-items: center;
}
.textBox {
  box-sizing: border-box;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  height: 100%;
  width: 100%;
  overflow: hidden;
  background-color: rgb(25, 25, 25);
  color: aliceblue;
  bottom: 0;
  position: relative;
}

.titleNameBox {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100%;
  width: 100%;
}

.nameBox {
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 50%;
  width: 20%;
}

.contactBox {
  height: 60%;
  width: 20%;
}

.socialBox {
  height: 50%;
  width: 20%;
  display: flex;
  flex-direction: column;
}

.contentsBox {
  height: 50%;
  width: 20%;
  display: flex;
  flex-direction: column;

  align-items: center;
}

.boxBox {
  width: 96%;
  height: 85%;

  display: flex;
  justify-content: space-evenly;
  align-items: center;
  bottom: 0;
  position: absolute;
}

.h1 {
  font-size: 26px;

  width: 100%;
  height: auto;
  color: aliceblue;
  padding-left: 10%;
  font-weight: bold;
  font-family: sans-serif;
}

.h2 {
  font-size: 1.2rem;
  margin-bottom: 5%;
}

@media screen and (max-width: 1024px) {
  .h2 {
    font-size: 1.1rem;
  }
}

@media screen and (max-width: 768px) {
  .h2 {
    font-size: 1rem;
  }
}

.h22 {
  font-size: 18px;
}
.icontext {
  width: 50%;
  margin-bottom: 5%;
}

.iconBox {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.iconTitleBox {
  width: 100%;
  display: flex;
  justify-content: center;

  height: 25%;
}

.ul {
  display: flex;
  flex-direction: column;

  list-style: none;

  width: fit-content;
  height: auto;
  padding-left: none;
  margin-left: none;
  position: absolute;
}

.li {
  font-size: 1.2rem;
  padding-bottom: 15%;
}

@media screen and (max-width:1024px){
  .li{
    font-size:1.1rem;
  }
}

@media screen and (max-width:768px){
  .li{
    font-size:1.1rem;
  }
}
.u {
  color: aliceblue;
  width: 50%;
}
.github {
  display: flex;
  flex-direction: row;
}
.linkedin {
  display: flex;
  flex-direction: row;
}
.icon {
  font-size: 36px;
  margin-bottom: 5%;
}
.link:hover {
  cursor: pointer;
  transform: scale(1.1);
}

.quote {
  font-size: 18px;
}

.a {
  text-decoration: none;
  color: aliceblue;
  margin-bottom: 2%;
}

.a:visited {
  text-decoration: none;
}

.a:hover {
  color: aliceblue;
}
